.landing {
  width: 100%;
  height: 100%;
  background-color: #0084a2;
}

.container {
  position: relative;
  width: 800px;
  margin: 0 auto;
  padding: 20px 0;
  text-align: center;
}

.lang {
  top: 30px;
  right: 30px;
  position: absolute;
}

.title {
  font: 100 48px Roboto;
  color: #fff;
  -webkit-font-smoothing: antialiased;
}

.description {
  margin-top: 20px;
  font: 100 18px Roboto;
  color: #fff;
}

.visualizerPage {
  text-align: center;
  height: 88%;
  padding: 0 30px;
}

.button {
  font: 100 36px Roboto;
  background-color: transparent;
  border: none;
  margin: 0 10px;
  color: #fff;
}

.visualizerIndex {
  font: 100 36px Roboto;
  color: #fff;
  width: 400px;
}



